import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhAlgorithm } from 'ionic-vhframeworks';

@Component({
  selector: 'app-modal-link-product-ecommerce',
  templateUrl: './modal-link-product-ecommerce.component.html',
  styleUrls: ['./modal-link-product-ecommerce.component.scss']
})
export class ModalLinkProductEcommerceComponent implements OnInit {
  @Input() visible_affiliate: boolean;
  @Input() searchProductAppValue: string;
  @Input() listProductApp: any[];
  @Input() title: string;

  @Output() searchProductApp = new EventEmitter<any>();
  @Output() acceptAfiliate = new EventEmitter<any>();
  @Output() cancelAffiliate = new EventEmitter<any>();


  setHeight = { "max-height": "500px", "height": "500px", "overflow-y": "hidden" };

  constructor(
    private vhAlgorithm: VhAlgorithm
  ) { }

  ngOnInit(): void {
  }

  handleSearchProductApp(event) {
    this.searchProductApp.emit(event);
  }

  handleCancelAffiliate() {
    this.cancelAffiliate.emit();
  }

  handleAcceptAfiliate(item) {
    this.acceptAfiliate.emit(item);
  }
}
